.corazon {
    @include fa-icon;
    @extend .fas;
  
    &:before {
      content: fa-content($fa-var-heart);
    }
  }
  .ball {
    @include fa-icon;
    @extend .fas;
  
    &:before {
      content: fa-content($fa-var-bowling-ball);
    }
  }
  .gem {
    @include fa-icon;
    @extend .fas;
  
    &:before {
      content: fa-content($fa-var-gem);
    }
  }
  .flag {
    @include fa-icon;
    @extend .fas;
  
    &:before {
      content: fa-content($fa-var-flag);
    }
  }
  .label-check.active, .check {
    @include fa-icon;
    @extend .fas;
  
    &:before {
      content: fa-content($fa-var-check);
    }
  }
  

