
.content,html, body{
    height: 100%;
}
.flex-parent{
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
    margin: 0;
    .flex-grow{
        flex:1;
        width: 40%;
        height: auto;
        align-self: center;
        justify-content: center;
    }
}

.video-view{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.embed-container {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
}
.embed-container iframe {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
}

a{        text-decoration: none;

    &:hover,&:active,&:focus{
        text-decoration: none;
        color: inherit;
    }
}
.icon{
    text-align: center;
    margin: auto;
    display: block;
    font-size: 2rem;
    color: lightslategray;}

.slider {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    @media (min-width:768px) {
        flex-wrap: nowrap;
    }

    .card{
        display: flex;
        flex-direction: column;
        margin: .7rem;
        border-radius: 20px;
        box-shadow: 0px 1px 1px 1px rgba(0,0,0,.3);
        overflow: hidden;

        

        transition: all .15s cubic-bezier(.33,.66,.66,1);
        transition-property: all;
        transition-duration: .15s;
        transition-timing-function: cubic-bezier(.33,.66,.66,1);
        transition-delay: initial;
        transform: translate3d(0,0,0);
        -webkit-transform-style: preserve-3d;
        -webkit-backface-visibility: hidden;

        &:hover{

      box-shadow: 0 7px 12px rgba(0,0,0,.45);
      transform: translate3d(0,-3px,0);
          
        }

        .card-header{
            color: #fff;
            padding: 1rem;
        
            
        }

        .card-body{
            padding: 1rem;
            color: #000;
        }
        &:nth-child(1){
            .card-header  {background-color: pink}
        }
        &:nth-child(2){
            .card-header { background-color: purple}
        }
        &:nth-child(3){
            .card-header {background-color: blue}
        }

        &:hover{
            .card-header{color: #fff}
            .card-body{color: gray}
        }
    }
}